<script context="module">
    export function preload() {
        return this.fetch("/api/meta").then((r) => r.json()).then((stats) => {
            return { stats };
        });
    }
</script>

<script>
    import { onMount } from "svelte";
    import { fly } from "svelte/transition";

    export let stats;

    let loaded = false;

    onMount(() => {
        loaded = true;
    })

</script>

<div class="viewbox">
    <h1 class="main-title">classic db bot</h1>
    {#if loaded}
        <div class="viewbox-content" transition:fly={{ y: 60, duration: 1200 }}>
            <div class="overview">
                <span>Used by {stats.guild_count} servers</span>
                <span>{stats.hits_count} items provided</span>
                <span>
                    Most requested item: <a href="https://itemization.info/item/{item_id}"></a>
                </span>
            </div>
        </div>
    {/if}
</div>

<style>
    .viewbox {
        display: flex;
        flex-direction: column;
        align-items: center;
        flex: 1;
        color: white;
    }

    .main-title {
        font-size: 66px;
        text-transform: uppercase;
        font-weight: 300;
    }
</style>